import uniPopup from "@/components/uni-popup/uni-popup.vue"
var graceChecker = require("../../../common/graceChecker.js");

var sourceType = [
	['camera'], //拍照
	['album'], //相册
	['camera', 'album'] //拍照或相册
]
var sizeType = [
	['compressed'], //压缩
	['original'], //原图
	['compressed', 'original'] //压缩或原图
]

export default {
	data() {
		return {
			user: {
				phone: "17872981234",
				idCard: "431172867264536121",
				password: "1234",
				confirmedPassword: "1234"
			},
			imageList: [], //保存图片路径集合
			imageLength: 3, //限制图片张数
			sourceTypeIndex: 2, //添加方式限制
			sizeTypeIndex: 2, //图片尺寸限制
		}
	},

	components: {
		uniPopup
	},

	methods: {
		formSubmit(e) {
			//将下列代码加入到对应的检查位置
			//定义表单规则
			var rule = [{
					name: "phone",
					checkType: "phoneno",
					checkRule: "",
					errorMsg: "请输入正确的手机号"
				},
				{
					name: "idCard",
					checkType: "same",
					checkRule: "18",
					errorMsg: "请输入正确的身份证号"
				},
				{
					name: "password",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "请输入正确的密码"
				},
				{
					name: "confirmedPassword",
					checkType: "notnull",
					checkRule: "",
					errorMsg: "请确认密码"
				}
			];
			//进行表单检查
			var formData = e.detail.value;
			var checkRes = graceChecker.check(formData, rule);
			var that = this;
			if (checkRes) {
				uni.uploadFile({
					url: 'http://192.168.1.104:8080/gongyv_manage/api/register.action',
					filePath: this.imageList[0],
					name: 'files',
					formData: {
						phone: this.user.phone,
						idCard: this.user.idCard,
						password: this.user.password,
						confirmedPassword: this.user.confirmedPassword
					},
					success: function(res) {
						if (res.data) {
							that.$refs.popup.open(); //弹出框，注册成功
							function out() {
								uni.navigateTo({
									url: '../login'
								});
							}
							setTimeout(out, 3000); //   在3000毫秒后只执行一次
						} else {
							that.$refs.popup2.open();
						}
					},

				})
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: "none"
				});
			}
		},

		chooseImage: function() {
			uni.chooseImage({
				sourceType: sourceType[this.sourceTypeIndex],
				sizeType: sizeType[this.sizeTypeIndex],
				count: this.imageLength - this.imageList.length,
				success: (res) => {
					this.imageList = this.imageList.concat(res.tempFilePaths);
				}
			})
		},
		//预览图片
		previewImage: function(e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
		//删除图片
		deleteImage: function(e) {
			var index = e.target.dataset.index;
			var that = this;
			var images = that.imageList;
			images.splice(index, 1);
			that.imageList = images;
		}



	}


}
